}

.contact-form input,
.contact-form textarea {
  background-color: #f8f9fa;
  border: none;
  border-radius: 0.4rem;
}

.contact-form input:focus,
.contact-form textarea:focus {
  box-shadow: none;
  outline: none;
  border-color: #0d6efd;
}
